.fg {
  position: relative;
  margin: 0;
  textarea,
  input ,
  .nice-select{
    border: $border;
    padding: 0 1.25em;
    height: 3.8125em;
    width: 100%;
    font-size: 1em;
    border-radius: .4375em;
    background-color: transparent;
    color: #2C333D;
    &::placeholder {
      color: #ADB1B7;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    height: 6.92em;
    resize: none;
  }
  label{
    transition: all .2s;
    font-size: .875em;
    line-height: 157%;
    display: block;
    margin-bottom: .5em;
  }
  &._required{
    label:after{
      content: ' *';
      color: $red;
    }
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.4375em;
    white-space: pre-wrap;
    &:focus{
      border: $border;
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .5em;
      height: .75em;
      background-image: url(../img/arrow-bottom.svg);
      @include bgImgSize(100%);
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -.25em;
      border-radius: 0 0 0.3125em 0.3125em;
      width: calc(100% + 2px);
      left: -1px;
      border: $border;
      background: #FFF;
      box-shadow: none;
      max-height: 11.75em;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 0.625em;
        height: 0.625em;
      }
      &::-webkit-scrollbar-track {
        background: #F3F6F9;
      }
      &::-webkit-scrollbar-thumb {
        background: rgba(#1B222B,.6);
        border-radius: 5.625em;
        width: 0.375em;
      }
    }
    .option{
      width: calc(100% + 2px);
      height: 3.8125em;
      font-size: 1em;
      line-height: 157%;
      padding: 0 1.07em;
      display: flex;
      align-items: center;
      color: #58595A;
      border: none;
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: #186FD4;
        font-weight: 300;
        color: #fff;
      }
    }
  }
}


.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    span{
      font-size: .9375em;
      line-height: 180%;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-image: url(../img/check.svg);
      }
    }
  }
  label {
    padding-left: 2.44em;
    min-height: 1.6875em;
    color: #363839;
    &:before {
      border-radius: .25em;
      border: 1px solid #DFE0E2;
      width: 1.625em;
      height: 1.625em;
      @include bgImgSize(.75em);
    }
  }
}
