.catalog-item{
  &__inner{
    display: grid;
    grid-template-columns: 50em;
    margin-top: 2.5em;
  }
}

.gallery{
  &__img{
    display: block;
    @include imgCover(76.2%);
    img{
      transition: all .3s;
    }
  }
  &__small{
    margin-top: .94em;
    width: calc(100% + .94em);
    .swiper{
      &-slide{
        height: 5.875em;
        padding-bottom: 1.25em;
        &:hover,
        &-thumb-active{
          img{
            border-color: $lightBlue;
          }
        }
      }
    }
    .gallery__img{
      padding: 0;
      height: 100%;
    }
  }
  &__big{
    position: relative;
    .gallery__img{
      padding: 0;
      height: 34em;
    }
  }
  .swiper{
    &-nav{
      display: flex;
      align-items: center;
      bottom: 3em;
      right: 1.875em;
      position: absolute;
    }
    &-button{
      position: static;
      width: .625em;
      height: auto;
      margin: 0 0 0 1.5em;
    }
  }
}

.zoom{
  position: absolute;
  left: 1.25em;
  bottom: 1.25em;
  width: 1em;
  pointer-events: none;
  z-index: 2;
  @include center;
  img{
    width: 100%;
    height: auto;
  }
}
