.text{
  color: #2C333D;
  p,li{
    font-size: .9375em;
    line-height: 173%;
  }
  p,ul{
    @include lastM(0 0 .9em);
  }
  ul{
    @include grid100gap(1em);
    li{
      position: relative;
      padding-left: .93em;
      &:before{
        content: '';
        position: absolute;
        top: .7em;
        left: 0;
        width: .33em;
        height: .33em;
        border-radius: 50%;
        background-color: #EE0906;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .text{
    ul{
      gap: .625em;
    }
    p,li{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    ul{
      gap: 1em;
    }
    ul,
    p{
      @include lastM(0 0 1em);
    }
  }
}
